/*  src/Header.css  */
.NavBar {
	height: 125px;
	background-color: rgb(224, 153, 153);
	position: fixed;
	top: 0;
	z-index: 999;
	width: 100%;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 6px 0 rgba(0, 0, 0, 0.19);
}

.nav-link {
	display: inline-block;
	padding: 0.5em 1em;
	color: rgba(169, 141, 141, 0.835);
	font-weight: bold;
	text-decoration: none;
}
.nav-link:hover {
	color: rgb(211, 113, 113);
}

.nav-link-active {
	color: rgb(106, 76, 76);
	border-bottom: 2px solid;
}

.Brand {
	padding-top: 10px;
	padding-bottom: 5px;
	font-family: 'Gloria Hallelujah', cursive;
}

