/*  src/Album/AlbumBook.css  */

.AlbumBook .aBook {
	border: none;
	position: relative;
}
.AlbumBook-Title {
	text-align: left;
	text-shadow: 1px 1px #c46060;
}

.AlbumBook-Detail {
	text-shadow: 1px 1px #c46060;
	text-align: right;
	position: absolute;
	right: 1em;
	bottom: 1em;
}

.AlbumBook {
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
img {
	border-radius: 20px;
	object-fit: cover;
	width: 100%;
	max-height: 100%;
	min-height: 300px;
}

.AddButton {
	border-radius: 30px;
}

.preview {
	display: flex;
	justify-content: center;
	min-height: 200px;
	margin-bottom: 1em;
}

.Album {
	margin-top: 160px;
}
.Greeting {
	font-family: 'Gloria Hallelujah', cursive;
	color: rgb(165, 162, 162);
}
.AlbumBooks {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	grid-auto-rows: 0.6fr;
	grid-gap: 2em;
	margin: auto;
	padding: 2em;
}

.Add-button {
	display: flex;
	flex-direction: column;
	justify-content: center;
}
/* Smaller screens and tablets */
@media only screen and (max-width: 960px) {
	.AlbumBooks {
		grid-template-columns: 1fr 1fr;
	}
}
/* Small screens mobile */
@media only screen and (max-width: 700px) {
	.AlbumBooks {
		grid-template-columns: 1fr;
	}
}

.Card {
	background-color: lightgrey;
	font-size: 12px;
	padding: 20px;
	margin-bottom: 10px;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 9px 0 rgba(0, 0, 0, 0.19);
}

.Card-grey {
	background-color: rgba(197, 179, 179, 0.427);
	color: black;
}

.Card-blue {
	background-color: rgb(160, 218, 240);
	color: white;
}

.Card-green {
	background-color: rgb(176, 208, 176);
	color: white;
}

.Card-title {
	font-weight: bold;
}

.Swimlane-title {
